
.loading {
  z-index: 999999;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
  height: 25px;
  width: 10px;
  border-radius: 10%;
  background: #2c3e50;
  border-top-color: #2980b9;
  animation: fade2 1s infinite;
  transition: background .2s;
}
.loading:after, .loading:before {
  content: '';
  position: absolute;
  display: block;
  height: 20px;
  width: 10px;
  background: #2c3e50;
  top: 50%;
  transform: translateY(-50%);
  left: -15px;
  border-radius: 10%;
  animation: fade1 1s infinite;
  transition: background .2s;
}
.loading:before {
  left: 15px;
  animation: fade3 1s infinite;
}
@keyframes fade1 {
0% {
    background: #2980b9;
}
}
@keyframes fade2 {
33% {
    background: #2980b9;
}
}
@keyframes fade3 {
66% {
    background: #2980b9;
}
}



